.Products {
    height: 550px;
    width: 600px;
    min-width: 300px;
    background: #fff;
    box-shadow: 0 3px 6px 0 grey;
    margin: 2em;
    padding: 1em;
}

.add-to-cart-button {
    display: flex;
    justify-content: center;
    align-items: center;
}

.fa-shopping-cart {
    border: 1px solid grey;
    border-radius: 5px;
    box-shadow: 0 1px 1px 1px lightgrey;
    color: #34d5ac;
    font-size: 24px;
    padding: 10px;
}

.fa-shopping-cart:hover {
    cursor: pointer;
    box-shadow: 0 1px 1px 0 lightgrey;
}

.fa-shopping-cart:active {
    box-shadow: 0 1px 1px 2px #34d5ac;
}


.product {
    border-bottom: 1px solid black;
    display: flex;
    justify-content: space-between;
    padding: 0.5em;
}

.product p {
    width: 33%;
}

.Discount {
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    margin-bottom: 1em;
    padding-top: 1em;
}

fieldset {
    border: none;
    display: block;

}

.radio-buttons {
    display: block;
}

.radio-container {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.radio-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  background: #eee;
  border-radius: 25%;
  border: 1px solid black;
}

.radio-container:hover input ~ .checkmark {
  background-color: #ccc;
}

/* When the radio button is checked, add a blue background */
.radio-container input:checked ~ .checkmark {
  background-color: #34d5ac;
}

.Cart {
    height: 550px;
    width: 450px;
    min-width: 300px;
    background: #fff;
    box-shadow: 0 3px 6px 0 grey;
    margin: 2em;
    padding: 1em;
}

.cart-item {
    display: flex;
    justify-content: space-between;
    padding: 0.5em;
}

.cart-item-quantity {
    display: flex;
}

.cart-item-quantity input {
    display: flex;
    font-family: "Open Sans", "Lucida Grande", Arial, sans-serif;
    padding: 2px;
    width: 48px;
}

.cart-item-quantity input:focus {
    outline: 2px solid #34d5ac;
}

.remove-item {
    display: flex;
    justify-content: center;
    align-items: center;
}

.fa-trash {
    margin-left: 15px;
}

.fa-trash:hover {
    color: #34d5ac;
    cursor: pointer
}

.item-subtotal {
    padding: 0.5em;
}

.item-total {
    padding: 0.5em;
    font-weight: bold;
}

